body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.frame {
  border: 1px solid black;
  margin: 5vh auto;
  display: flex;
  flex-direction: column;
  box-sizing: border-box; }
  .frame .header {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid black; }
    .frame .header h4 {
      margin: 0; }
  .frame .body {
    flex: 1 1;
    display: flex;
    flex-direction: column; }
  .frame.visible.editor {
    height: 30vh;
    width: 50vw; }
  .frame.visible.previewer {
    min-height: 50vh;
    width: 70vw; }
  .frame.expanded {
    min-height: 95vh;
    width: 95vw; }
  .frame.hidden {
    display: none; }
  .frame.editor textarea {
    position: relative;
    width: calc(100% - 4px);
    flex: 1 1;
    resize: none;
    border: none;
    outline: none; }
  .frame #preview {
    padding: 1em 2em; }

